.tiles {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
}

.tiles>div {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
}

.tiles>div:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.list {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
}

.list>div {
    background-color: #fff;
    padding: 10px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
}

.list>div:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.notSelected {
    opacity: 0.5;
}

.selected {
    opacity: 1;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

input[type=range]::-moz-range-track {
    background-color: blue;
}

input[type=range]:focus {
    outline: none;
}

@media (max-height: 830px) {
    html {
        zoom: .8;
    }
}

@media (min-width: 960px) {
    .screen {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-auto-rows: 100vh;
        animation: fadein 500ms;
    }
    @keyframes fadein {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
    .leftContainer>span>form>div>img {
        height: 100px;
        width: auto;
    }
    .leftContainer {
        position: absolute;
        top: 50%;
        left: 25%;
        transform: translate(-50%, -50%);
        width: 350px;
    }
    .rightContainer {
        position: absolute;
        top: 50%;
        left: 75%;
        transform: translate(-50%, -50%) scale(0.9);
    }
    .rightContainer>img {
        height: auto;
        width: 500px;
    }
    .imageAndBlocks {
        display: grid;
        grid-template-columns: 1fr;
    }
    .LeftRight {
        position: absolute;
        top: 900px;
        left: 49%;
        transform: translate(-50%, 0);
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 250px;
        font-weight: bold;
    }
}

